<template>
    <view class="camera-page">
        <!-- Áõ∏Êú∫ÁªÑ‰ª∂ÔºàÂÖ®Â±èÔºâ -->
        <camera class="camera" :device-position="cameraPosition" :flash="flash" v-if="showCamera">
            <!-- ÂÄíËÆ°Êó∂ -->
            <cover-view class="countdown" v-if="countdown > 0">
                <cover-view class="countdown-number">{{ countdown }}</cover-view>
            </cover-view>
        </camera>

        <!-- ÂßøÂäøÂèÇËÄÉÂõæÔºàÂ±Ö‰∏≠ÔºåÂçäÈÄèÊòéÔºâ -->
        <image src="/static/girl-pose-01.png" class="pose-reference" :style="{ opacity: 0.5 }" />

        <!-- Ëá™ÂÆö‰πâÈ°∂ÈÉ®ÂØºËà™Ê†è -->
        <view class="custom-navbar">
            <!-- ËøîÂõûÊåâÈíÆ -->
            <view class="nav-left" @click="goBack">
                <image src="/static/arrow_left.png" class="nav-icon" />
            </view>
        </view>

        <!-- Âõ∫ÂÆöÂú®Â∫ïÈÉ®ÁöÑÊìç‰ΩúÊ†è -->
        <view class="fixed-action-bar">
            <!-- ÂßøÂäøÈÄâÊã©ÊèêÁ§∫Ê°Ü -->
            <view class="pose-tip" v-if="showPoseTip" :class="{ 'fade-out': isTipFading }" @click="hidePoseTip">
                <view class="tip-content">
                    <text class="tip-icon">üëá</text>
                    <text class="tip-text">Ë∞ÉÊï¥ÂèÇÊï∞</text>
                </view>
                <view class="tip-arrow"></view>
            </view>
            <view class="action-items">
                <!-- Â∑¶‰æßÔºöÁõ∏ÂÜåÈ¢ÑËßà -->
                <view class="action-item album-preview" @click="showSettings">
                    <image src="/static/girl-pose-01.png" class="preview-image" />
                </view>

                <!-- ‰∏≠Â§ÆÔºöÊãçÁÖßÊåâÈíÆ -->
                <view class="action-item shutter-container" @click="takePhoto">
                    <view class="shutter-button">
                        <view class="shutter-circle">
                            <view class="loading" v-if="isCapturing"></view>
                        </view>
                    </view>
                </view>

                <!-- Âè≥‰æßÔºöÂàáÊç¢ÊëÑÂÉèÂ§¥ -->
                <view class="action-item switch-camera" @click="switchCamera">
                    <image src="/static/switch_camera.png" class="switch-icon" />
                </view>
            </view>
        </view>
    </view>

    <!-- ËÆæÁΩÆÂºπÁ™ó -->
    <view class="settings-modal" v-if="showSettingsModal" @click="closeSettings">
        <view class="settings-content" @click.stop>
            <view class="settings-header">
                <text class="settings-title">ÊãçÁÖßËÆæÁΩÆ</text>
                <text class="close-btn" @click="closeSettings">‚úï</text>
            </view>

            <!-- ‰øÆÊîπËøôÈáåÔºöÁªô scroll-view Ê∑ªÂä†ÊòéÁ°ÆÁöÑÊ†∑ÂºèÁ±ª -->
            <scroll-view class="settings-body" scroll-y :scroll-top="scrollTop" @scroll="onScroll">
                <!-- ÈÄèÊòéÂ∫¶ËÆæÁΩÆ -->
                <view class="setting-item">
                    <view class="setting-label">
                        <text class="label-text">ÂèÇËÄÉÂõæÈÄèÊòéÂ∫¶</text>
                        <text class="label-value">{{ Math.round(settings.opacity * 100) }}%</text>
                    </view>
                    <view class="slider-container">
                        <slider class="opacity-slider" :value="settings.opacity * 100" min="10" max="90" step="5"
                            activeColor="#007AFF" backgroundColor="#E5E5E5" block-size="20" @changing="onOpacityChange"
                            @change="onOpacityChange" />
                    </view>
                </view>

                <!-- ÂÄíËÆ°Êó∂ËÆæÁΩÆ -->
                <view class="setting-item">
                    <view class="setting-label">
                        <text class="label-text">ÂÄíËÆ°Êó∂Êó∂Èïø</text>
                        <text class="label-value">{{ settings.countdownDuration }}Áßí</text>
                    </view>
                    <view class="countdown-options">
                        <view class="countdown-option" v-for="duration in countdownOptions" :key="duration"
                            :class="{ active: settings.countdownDuration === duration }"
                            @click="setCountdownDuration(duration)">
                            {{ duration }}Áßí
                        </view>
                    </view>
                </view>

                <!-- ÊòæÁ§∫Ê®°ÂºèËÆæÁΩÆ -->
                <view class="setting-item">
                    <view class="setting-label">
                        <text class="label-text">ÂèÇËÄÉÂõæÊòæÁ§∫Ê®°Âºè</text>
                    </view>
                    <view class="display-modes">
                        <view class="display-mode" :class="{ active: settings.displayMode === 'background' }"
                            @click="setDisplayMode('background')">
                            <view class="mode-info">
                                <image class="mode-icon"
                                    :src="settings.displayMode === 'background' ? '/static/background-active.png' : '/static/background.png'">
                                </image>
                                <text class="mode-title">ËÉåÊôØÂõæÊ®°Âºè</text>
                            </view>
                            <text class="mode-desc">Âõ∫ÂÆöÂú®Â±èÂπï‰∏≠Â§Æ‰Ωú‰∏∫ËÉåÊôØ</text>
                        </view>

                        <view class="display-mode" :class="{ active: settings.displayMode === 'float' }"
                            @click="setDisplayMode('float')">
                            <view class="mode-info">
                                <image class="mode-icon"
                                    :src="settings.displayMode === 'float' ? '/static/float-active.png' : '/static/float.png'">
                                </image>
                                <text class="mode-title">ÊµÆÁ™óÊ®°Âºè</text>
                            </view>
                            <text class="mode-desc">ÂèØÊãñÂä®Ë∞ÉÊï¥‰ΩçÁΩÆÁöÑÊµÆÁ™ó</text>
                        </view>
                    </view>
                </view>

                <!-- ÂÖ∂‰ªñËÆæÁΩÆ -->
                <view class="setting-item">
                    <view class="setting-label">
                        <text class="label-text">ÂÖ∂‰ªñÈÄâÈ°π</text>
                    </view>
                    <view class="switch-options">
                        <view class="switch-option">
                            <view class="option-header">
                                <text class="switch-label">ÈúáÂä®ÂèçÈ¶à</text>
                                <switch :checked="settings.vibration" color="#007AFF" @change="onVibrationChange" />
                            </view>
                        </view>
                        <view class="switch-option">
                            <view class="option-header">
                                <text class="switch-label">Âø´Èó®Èü≥Êïà</text>
                                <switch :checked="settings.shutterSound" color="#007AFF"
                                    @change="onShutterSoundChange" />
                            </view>
                        </view>
                        <view class="switch-option">
                            <view class="option-header">
                                <text class="switch-label">ÁΩëÊ†ºËæÖÂä©Á∫ø</text>
                                <switch :checked="settings.showGrid" color="#007AFF" @change="onGridChange" />
                            </view>

                            <transition name="fade">
                                <view class="grid-container" v-if="settings.showGrid">
                                    <view class="option-item" v-for="(option, index) in settings.options" :key="index">
                                        <radio class="radio" :value="option.value"
                                            :checked="settings.selectedOption === option.value"
                                            :disabled="!settings.showGrid" @click="selectOption(option.value)" />
                                        <text class="option-label">{{ option.label }}</text>
                                    </view>
                                </view>
                            </transition>
                        </view>
                    </view>
                </view>

                <view class="setting-item">
                    <view class="settings-footer">
                        <view class="reset-btn" @click="resetSettings">
                            <text>ÊÅ¢Â§çÈªòËÆ§</text>
                        </view>
                        <view class="save-btn" @click="saveSettings"><text>‰øùÂ≠òËÆæÁΩÆ</text></view>
                    </view>
                </view>

                <!-- Ê∑ªÂä†Â∫ïÈÉ®Èó¥Ë∑ùÔºåÁ°Æ‰øùÊúÄÂêé‰∏ÄÈ°πÂèØ‰ª•ÂÆåÂÖ®ÊòæÁ§∫ -->
                <view class="scroll-bottom-padding"></view>
            </scroll-view>
        </view>
    </view>
</template>

<script>
    export default {
        data() {
            return {
                showCamera: true,
                cameraPosition: 'back',
                flash: 'off',
                isCapturing: false,
                countdown: 0,
                showPoseTip: false, // ÊéßÂà∂ÊèêÁ§∫Ê°ÜÊòæÁ§∫
                isTipFading: false, // ÊéßÂà∂Ê∑°Âá∫Âä®Áîª
                tipTimer: null, // ÊèêÁ§∫Ê°ÜÂÆöÊó∂Âô®,
                showSettingsModal: false,

                // ÊªöÂä®Áõ∏ÂÖ≥
                scrollTop: 0,
                isScrolling: false,
                scrollTimer: null,

                // ËÆæÁΩÆÁõ∏ÂÖ≥
                settings: {
                    opacity: 0.3, // ÈÄèÊòéÂ∫¶
                    countdownDuration: 3, // ÂÄíËÆ°Êó∂Êó∂ÈïøÔºàÁßíÔºâ
                    displayMode: 'background', // ÊòæÁ§∫Ê®°Âºè: 'background' | 'float'
                    vibration: true, // ÈúáÂä®ÂèçÈ¶à
                    shutterSound: true, // Âø´Èó®Èü≥Êïà
                    showGrid: false,

                    // ÂΩìÂâçÈÄâ‰∏≠ÁöÑÂèÇËÄÉÁ∫øÁ±ªÂûã
                    selectedOption: "grid",

                    // ÊâÄÊúâÂèÇËÄÉÁ∫øÈÄâÈ°π
                    options: [{
                            value: "grid",
                            label: "‰πùÂÆ´Ê†ºÂèÇËÄÉÁ∫ø"
                        },
                        {
                            value: "golden",
                            label: "ÈªÑÈáëÂàÜÂâ≤Á∫ø"
                        },
                        {
                            value: "spiral",
                            label: "ÈªÑÈáëËû∫ÊóãÁ∫ø"
                        },
                        {
                            value: "diagonal",
                            label: "ÂØπËßíÁ∫øÂèÇËÄÉÁ∫ø"
                        },
                        {
                            value: "thirds",
                            label: "‰∏âÂàÜÊ≥ïÂèÇËÄÉÁ∫ø"
                        }
                    ]
                },

                // ÂÄíËÆ°Êó∂ÈÄâÈ°π
                countdownOptions: [0, 1, 3, 5, 10],

                // ÊµÆÁ™óÊãñÊãΩÁõ∏ÂÖ≥
                floatPosition: {
                    x: 50,
                    y: 200
                },
                isDragging: false,
                dragStartPos: {
                    x: 0,
                    y: 0
                }
            }
        },
        // Ê∑ªÂä† onReady ÁîüÂëΩÂë®Êúü
        onReady() {
            // È°µÈù¢Ê∏≤ÊüìÂÆåÊàêÂêéÊòæÁ§∫ÊèêÁ§∫
            this.showPoseSelectionTip();
        },

        // Âú® onHide ‰∏≠Ê∏ÖÁêÜÂÆöÊó∂Âô®
        onHide() {
            this.clearTipTimer();
        },

        // Âú® onUnload ‰∏≠Ê∏ÖÁêÜÂÆöÊó∂Âô®
        onUnload() {
            this.clearTipTimer();
        },
        computed: {
            isIOS() {
                const platform = uni.getSystemInfoSync().platform;
                return platform === 'ios' || platform === 'mac';
            }
        },
        methods: {
            switchCamera() {
                this.cameraPosition = this.cameraPosition === 'back' ? 'front' : 'back';
            },
            takePhoto() {
                // ÊãçÁÖßÈÄªËæë
                if (this.isCapturing) return;
                this.isCapturing = true;
                this.startCountdown();
            },
            goBack() {
                uni.navigateBack();
            },
            // ÂÄíËÆ°Êó∂
            startCountdown() {
                this.countdown = 3;
                const timer = setInterval(() => {
                    this.countdown--;

                    if (this.countdown <= 0) {
                        // ÈúáÂä®ÂèçÈ¶à
                        uni.vibrateShort();
                        clearInterval(timer);
                        this.capturePhoto();
                    }
                }, 1000);
            },
            // ÊâßË°åÊãçÁÖß
            capturePhoto() {
                const ctx = uni.createCameraContext();

                ctx.takePhoto({
                    quality: 'high',
                    success: (res) => {
                        this.capturedImage = res.tempImagePath;
                        this.isCapturing = false;

                        // Êí≠ÊîæÊãçÁÖßÈü≥Êïà
                        this.playShutterSound();
                    },
                    fail: (err) => {
                        console.log('ÊãçÁÖßÂ§±Ë¥•:', err);
                        this.isCapturing = false;
                        uni.showToast({
                            title: 'ÊãçÁÖßÂ§±Ë¥•',
                            icon: 'none'
                        });
                    }
                });
            },

            // Êí≠ÊîæÂø´Èó®Èü≥Êïà
            playShutterSound() {
                const audioContext = uni.createInnerAudioContext();
                audioContext.src = '/static/audio/shutter.mp3';
                audioContext.play();
            },

            // ÊòæÁ§∫ÂßøÂäøÈÄâÊã©ÊèêÁ§∫
            showPoseSelectionTip() {
                // Âª∂Ëøü500msÊòæÁ§∫ÔºåÁ°Æ‰øùÈ°µÈù¢ÂÆåÂÖ®Âä†ËΩΩ
                setTimeout(() => {
                    this.showPoseTip = true;

                    // 2ÁßíÂêéÂºÄÂßãÊ∑°Âá∫Âä®Áîª
                    this.tipTimer = setTimeout(() => {
                        this.startTipFadeOut();
                    }, 2000);
                }, 500);
            },

            // ÂºÄÂßãÊ∑°Âá∫Âä®Áîª
            startTipFadeOut() {
                this.isTipFading = true;

                // Âä®ÁîªÂÆåÊàêÂêéÈöêËóèÊèêÁ§∫Ê°Ü
                setTimeout(() => {
                    this.showPoseTip = false;
                    this.isTipFading = false;
                }, 300); // 300ms Âä®ÁîªÊó∂Èó¥
            },

            // Ê∏ÖÁêÜÂÆöÊó∂Âô®
            clearTipTimer() {
                if (this.tipTimer) {
                    clearTimeout(this.tipTimer);
                    this.tipTimer = null;
                }
            },

            // ÊâãÂä®ÈöêËóèÊèêÁ§∫Ê°ÜÔºàÂèØÈÄâÔºåÁî®Êà∑ÁÇπÂáªÊèêÁ§∫Ê°ÜÊó∂Ôºâ
            hidePoseTip() {
                this.clearTipTimer();
                this.startTipFadeOut();
            },

            // ËÆæÁΩÆÁõ∏ÂÖ≥ÊñπÊ≥ï
            showSettings() {
                this.hidePoseTip();
                this.showSettingsModal = true;
            },

            closeSettings() {
                this.showSettingsModal = false;
            },

            // ÈÄèÊòéÂ∫¶Ë∞ÉÊï¥
            onOpacityChange(e) {
                this.settings.opacity = e.detail.value / 100;
            },

            // ÂÄíËÆ°Êó∂Êó∂ÈïøËÆæÁΩÆ
            setCountdownDuration(duration) {
                this.settings.countdownDuration = duration;
            },

            // ÊòæÁ§∫Ê®°ÂºèËÆæÁΩÆ
            setDisplayMode(mode) {
                this.settings.displayMode = mode;
                if (mode === 'float') {
                    this.initFloatPosition();
                }
            },

            // ÂºÄÂÖ≥ËÆæÁΩÆ
            onVibrationChange(e) {
                this.settings.vibration = e.detail.value;
            },

            onShutterSoundChange(e) {
                this.settings.shutterSound = e.detail.value;
            },

            onAutoCaptureChange(e) {
                this.settings.autoCapture = e.detail.value;
            },

            // ÈáçÁΩÆËÆæÁΩÆ
            resetSettings() {
                uni.showModal({
                    title: 'Á°ÆËÆ§ÈáçÁΩÆ',
                    content: 'ÊòØÂê¶ÊÅ¢Â§çÊâÄÊúâËÆæÁΩÆ‰∏∫ÈªòËÆ§ÂÄºÔºü',
                    success: (res) => {
                        if (res.confirm) {
                            this.settings = {
                                opacity: 0.3,
                                countdownDuration: 3,
                                displayMode: 'background',
                                vibration: true,
                                shutterSound: true,
                                autoCapture: false
                            };
                            uni.showToast({
                                title: 'Â∑≤ÊÅ¢Â§çÈªòËÆ§ËÆæÁΩÆ',
                                icon: 'success'
                            });
                        }
                    }
                });
            },

            // ‰øùÂ≠òËÆæÁΩÆ
            saveSettings() {
                uni.showToast({
                    title: 'ËÆæÁΩÆÂ∑≤‰øùÂ≠ò',
                    icon: 'success'
                });
                this.closeSettings();
            },

            // ÊµÆÁ™óÊãñÊãΩÁõ∏ÂÖ≥ÊñπÊ≥ï
            initFloatPosition() {
                const systemInfo = uni.getSystemInfoSync();
                this.floatPosition = {
                    x: systemInfo.windowWidth - 200,
                    y: 200
                };
            },

            // ÊªöÂä®Áõ∏ÂÖ≥ÊñπÊ≥ï - ‰ºòÂåñ
            onScroll(e) {
                // Èò≤ÊäñÂ§ÑÁêÜÔºåÂáèÂ∞ëÈ¢ëÁπÅÊõ¥Êñ∞
                if (this.scrollTimer) {
                    clearTimeout(this.scrollTimer);
                }

                this.isScrolling = true;
                this.scrollTimer = setTimeout(() => {
                    this.isScrolling = false;
                }, 150);

                // Âè™Âú®ÂøÖË¶ÅÊó∂Êõ¥Êñ∞scrollTop
                const newScrollTop = e.detail.scrollTop;
                if (Math.abs(newScrollTop - this.scrollTop) > 5) {
                    this.scrollTop = newScrollTop;
                }
            },

            onScrollToUpper() {
                // Âà∞ËææÈ°∂ÈÉ®Êó∂ÁöÑÂ§ÑÁêÜ
                console.log('ÊªöÂä®Âà∞È°∂ÈÉ®');
            },

            onScrollToLower() {
                // Âà∞ËææÂ∫ïÈÉ®Êó∂ÁöÑÂ§ÑÁêÜ  
                console.log('ÊªöÂä®Âà∞Â∫ïÈÉ®');
            },

            // Êñ∞Â¢ûÁöÑËÆæÁΩÆÊñπÊ≥ï
            onSaveOriginalChange(e) {
                this.settings.saveOriginal = e.detail.value;
            },

            onWatermarkChange(e) {
                this.settings.addWatermark = e.detail.value;
            },

            onGridChange(e) {
                this.settings.showGrid = e.detail.value;

                if (this.settings.showGrid && !this.settings.selectedOption) {
                    this.settings.selectedOption = "grid";
                }
            },

            // ÈÄâÊã©ÂèÇËÄÉÁ∫øÁ±ªÂûã
            selectOption(value) {
                if (!this.settings.showGrid) return;
                this.settings.selectedOption = value;
            }
        }
    }
</script>

<style lang="scss" scoped>
    .camera-page {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100vh;
        background-color: #000;
    }

    .camera {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 1;
    }

    .countdown {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);

        .countdown-number {
            color: white;
            font-size: 120rpx;
            font-weight: bold;
            text-shadow: 0 0 20rpx rgba(0, 0, 0, 0.8);
            animation: countdownPulse 1s ease-in-out;
        }
    }

    @keyframes countdownPulse {
        0% {
            transform: scale(0.5);
            opacity: 0;
        }

        50% {
            transform: scale(1.2);
            opacity: 1;
        }

        100% {
            transform: scale(1);
            opacity: 1;
        }
    }

    .pose-reference {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 80%;
        height: 70%;
        z-index: 2;
    }

    .custom-navbar {
        position: absolute;
        top: var(--status-bar-height);
        left: 0;
        right: 0;
        height: 90rpx;
        /* Ëá™ÂÆö‰πâÂØºËà™Ê†èÈ´òÂ∫¶ */
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0 10rpx;
        z-index: 3;
    }

    .nav-left,
    .nav-right {
        width: 60rpx;
        height: 60rpx;
        display: flex;
        align-items: center;
        justify-content: center;
        padding-top: 50rpx;
    }

    .nav-icon {
        width: 40rpx;
        height: 40rpx;
    }

    /* Âõ∫ÂÆöÂú®Â∫ïÈÉ®ÁöÑÊìç‰ΩúÊ†è */
    .fixed-action-bar {
        position: fixed;
        bottom: 0;
        left: 0;
        right: 0;
        z-index: 100;
        width: 100%;
        background: rgba(0, 0, 0, 0.8);
        backdrop-filter: blur(20px);
        border-top: 1rpx solid rgba(255, 255, 255, 0.1);
        padding: 40rpx 0;
    }

    // ÂßøÂäøÈÄâÊã©ÊèêÁ§∫Ê°Ü
    .pose-tip {
        position: absolute;
        bottom: 150rpx;
        left: 80rpx;
        z-index: 200;
        animation: tipFadeIn 0.3s ease-out;

        &.fade-out {
            animation: tipFadeOut 0.3s ease-out;
        }

        .tip-content {
            background: white;
            color: black;
            padding: 10rpx 15rpx;
            border-radius: 10rpx;
            font-size: 24rpx;
            display: flex;
            align-items: center;
            gap: 10rpx;
            backdrop-filter: blur(10rpx);
            border: 1rpx solid rgba(255, 255, 255, 0.2);
            box-shadow: 0 8rpx 32rpx rgba(0, 0, 0, 0.3);

            .tip-icon {
                font-size: 24rpx;
                animation: pointDown 1s ease-in-out infinite;
            }

            .tip-text {
                white-space: nowrap;
            }
        }

        // ÁÆ≠Â§¥ÊåáÂêëÂßøÂäøÈ¢ÑËßà
        .tip-arrow {
            width: 0;
            height: 0;
            border-left: 15rpx solid transparent;
            border-right: 15rpx solid transparent;
            border-top: 20rpx solid rgba(0, 0, 0, 0.8);
            margin: 0 auto;
            position: relative;

            &::before {
                content: '';
                position: absolute;
                top: -22rpx;
                left: -16rpx;
                width: 0;
                height: 0;
                border-left: 16rpx solid transparent;
                border-right: 16rpx solid transparent;
                border-top: 22rpx solid rgba(255, 255, 255, 1);
            }
        }
    }

    // ÊèêÁ§∫Ê°ÜÂä®Áîª
    @keyframes tipFadeIn {
        0% {
            opacity: 0;
